<template>
  <nav class="dropdown" ref="dropdown">
    <button
      type="button"
      @click="toggle"
      ref="caller"
      :class="['dropdown-auth', isOpen ? 'active' : '']"
    >
      <a
        :href="links.support"
        class="message"
        v-if="!itsAdmin && newMessage"
        title="New message from support"
      >
        <font-awesome-icon icon="envelope" class="message-icon" />
        <div class="message-count">{{ formatedMessagesCount }}</div>
      </a>
      <span class="name">{{ name }}</span>
      <span class="balance">{{ balance }}$</span>
      <span class="angle">
        <font-awesome-icon :icon="isOpen ? 'angle-up' : 'angle-down'" />
      </span>
    </button>
    <ul v-if="isOpen" @click="close">
      <li v-if="itsAdmin">
        <a :href="links.adminPanel">Admin Panel</a>
      </li>
      <li class="divider" v-if="itsAdmin"></li>
      <li>
        <router-link :to="links.profile">Profile</router-link>
      </li>
      <li>
        <a @click="updateExchangeTab('wallet')" :href="links.wallet">Wallet</a>
      </li>
      <li>
        <a @click="updateExchangeTab('history')" :href="links.history"
          >History</a
        >
      </li>
      <li>
        <a
          @click="updateExchangeTab('support')"
          v-if="!itsAdmin"
          :href="links.support"
          >Support</a
        >
      </li>
      <li class="divider"></li>
      <li>
        <a href="#" @click="logout">Sign Out</a>
      </li>
    </ul>
  </nav>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Dropdown',
  data() {
    return {
      name: 'Username',
      balance: '0',
      isOpen: false,
      links: {
        profile: '/account',
        wallet: '/wallet#',
        history: '/wallet#history',
        support: '/wallet#support',
        adminPanel: '/admin',
      },
    }
  },
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
      wallets: 'auth/wallets',
    }),
    formatedMessagesCount() {
      let count = this.profile ? this.profile.newMessage : 0
      return count < 10 ? count : '9+'
    },
    adminToken() {
      return sessionStorage.getItem('admin-token')
    },
    itsAdmin() {
      return !['user'].includes(this.profile.role.name)
    },
    newMessage() {
      console.log(this.profile.newMessage)
      return this.profile && this.profile.newMessage
    },
  },
  watch: {
    profile(value) {
      this.updateName(value)
      this.updateBalance(value)
    },
    wallets(value) {
      this.$anime({
        targets: this.$el,
        scale: [0.9, 1],
        duration: 700,
        easing: 'easeInOutSine',
      })

      let balance = Object.values(value).reduce(
        (b, w) => b + w.balance * w.price,
        0,
      )
      this.balance = Number(balance.toFixed(2))
    },
  },
  methods: {
    updateName(profile) {
      const addLimit = this.balance.toString().length

      if (profile) {
        if (profile.lastName && profile.firstName) {
          this.name =
            this.profile.firstName.toLowerCase().capitalize() +
            ' ' +
            this.profile.lastName[0].toUpperCase() +
            '.'
        } else if (profile.firstName) {
          let separatedEmail = this.profile.firstName.split('@')
          this.name =
            separatedEmail.length > 1
              ? separatedEmail[0]
              : this.profile.firstName
        }

        if (this.name.length >= 16 - addLimit)
          this.name =
            this.name.match(new RegExp(`.{${16 - addLimit}}`))[0] + '...'
      } else {
        this.name = 'Username'
      }
    },
    updateBalance(profile) {
      this.balance = Number(
        Object.values(profile.wallets)
          .reduce((b, w) => b + w.balance * w.price, 0)
          .toFixed(2),
      )

      profile.balance = this.balance
    },
    toggle() {
      this.isOpen = !this.isOpen
    },
    closeOutside(event) {
      const { dropdown } = this.$refs

      if (!dropdown || dropdown.contains(event.target)) return
      this.isOpen = false
    },
    close() {
      this.isOpen = false
    },
    logout() {

      if (!this.adminToken || this.itsAdmin) {
        if (this.$route.name != 'Main') this.$router.push('/')
        this.$store.commit('auth/SET_PROFILE', {
          profile: null,
        })
      } else {
        sessionStorage.setItem('auth-token', this.adminToken)
        sessionStorage.removeItem('admin-token')
        window.location.href = '/'
      }
    },
    updateExchangeTab(hash) {
      if (hash === 'wallet') {
        this.$store.commit('popups/SELECT_EXCHANGE_TAB', 0)
      } else if (hash === 'history') {
        this.$store.commit('popups/SELECT_EXCHANGE_TAB', 4)
      } else if (hash === 'support') {
        this.$store.commit('popups/SELECT_EXCHANGE_TAB', 5)
      }
    },
  },
  mounted() {
    document.addEventListener('click', this.closeOutside)

    let profile = this.$store.getters['auth/profile']

    if (profile) {
      this.updateName(profile)
      this.updateBalance(profile)
    }
  },
  destroyed() {
    document.removeEventListener('click', this.closeOutside)
  },
}
</script>

